@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;500;700&display=swap);
:root{
    /* ### Primary */
    --dark-blue: hsl(240, 38%, 20%);
    --grayish-blue: hsl(240, 18%, 77%);
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body{
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    color: hsl(240, 38%, 20%);
    color: var(--dark-blue);
    display: flex;
    justify-content: center;
    background-image: url(/testimonials-slider/static/media/pattern-bg.7f46e67e.svg), url(/testimonials-slider/static/media/pattern-quotes.219bd182.svg), url(/testimonials-slider/static/media/pattern-curve.05698a95.svg);
    background-repeat: no-repeat, no-repeat, no-repeat;
    background-size: 330px, 60px, 375px;
    background-position: center 10%, center 55%, center bottom;
}

